import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message, Spin } from 'antd';
const { Content } = Layout;
import SearchForm from './Form/searchForm.jsx'
const selectOpsConfig = {
    catogoryList: {
        0: "所有",
        1: "债权",
        2: "股权",
        4: "资产转让"
    }
};
export class List extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            columns: [{
                title: '类别',
                render: info => <div>{selectOpsConfig.catogoryList[info.project.category]}</div>
            }, {
                title: '标题',
                render: info => <div>{info.project.title}</div>
            }, {
                title: '进度',
                dataIndex: 'progress',
                render: progress => progress == "1" ? "已结束" : "进行中"
            }, {
                title: "发布时间",
                dataIndex: 'publisherTime'
            }, {
                title: "收到的投递项目",
                render: data =>
                    <div className="divClick" onClick={this.showReceivedItems.bind(this, data.project.key)}>{data.receivedItemsNum}</div>

            }, {
                title: "操作",
                render: data => <div style={{"textAlign": "center"}}>
                    <Button type="primary" onClick={this.detailInfo.bind(this, data)} style={{"marginRight": "20px"}}>查看
                    </Button>
                    <Button onClick={this.editInfo.bind(this, data)} style={{"marginRight": "20px"}}>修改
                    </Button>
                    <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteInfo.bind(this, data.project.key)} okText="确认" cancelText="取消">
                        <Button type="danger">删除</Button>
                    </Popconfirm></div>
            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            },
            loading: false
        };
        ['showReceivedItems', 'changePage', 'handleSearch', 'showLoading', 'hideLoading'].forEach(m=>this[m] = this[m].bind(this))
    }

    showReceivedItems(id) {
        hashHistory.push("/delegateProject/receivedFund/list?projectID=" + id);
    }

    componentDidMount() {
        this.formRef.handleSubmit(1)
    }

    showLoading() {
        this.setState({loading: true});
    }

    hideLoading() {
        this.setState({loading: false});
    }

    detailInfo(data) {
        hashHistory.push("/delegateProject/detail/" + data.project.key + "?assetsType=" + data.project.category);
    }

    editInfo(data) {
        hashHistory.push("/delegateProject/edit/" + data.project.key + "?assetsType=" + data.project.category);
    }

    deleteInfo(key) {
        let that = this;
        ajax({
            url: "/delegateProject/deleteInfo",
            method: "post",
            data: {id: key},
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.project.key != key});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        });
    }

    handleSearch(result) {
        this.setState(
            {
                pagination: {
                    current: result.current,
                    total: result.total
                },
                dataSource: result.list
            }
        )
    }

    changePage(pagination) {
        this.formRef.handleSubmit(pagination.current);
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <Spin spinning={this.state.loading} tip="Loading...">
                        <div style={{"marginBottom": "30px"}}>
                            <SearchForm searchFuc={this.handleSearch} showLoading={this.showLoading} hideLoading={this.hideLoading} wrappedComponentRef={(inst) => this.formRef = inst} />
                        </div>
                        <Table
                            bordered
                            dataSource={this.state.dataSource}
                            columns={this.state.columns}
                            pagination={this.state.pagination}
                            onChange={this.changePage}
                        />
                    </Spin>
                </Content>
            </Layout>
        )
    }
}

module.exports = List;


