import React,{Component} from 'react'
import { hashHistory} from 'react-router'
import { Form, Select, Input, Button, message, Icon, Menu, Dropdown } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;

export class SearchForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            keyWord: "",
            type: "",
            status: ""
        };
        ['handleSubmit',  'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    handleSubmit(page) {
        let that = this;
        this.props.form.validateFields((err, values) => {
            that.props.showLoading();
            ajax({
                url: "/delegateProject/getInfoList",
                method: "post",
                data: Object.assign(values, {
                    current: page
                }),
                callback: function(data) {
                    that.props.hideLoading();
                    if(data.status == 200) {
                        that.props.searchFuc(data.data);
                    } else {
                        message.error(data.errMsg, 1);
                    }
                }
            })
        })
    }
    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const menu = (
            <Menu>
                <Menu.Item>
                    <a rel="noopener noreferrer" href="#/delegateProject/new?assetsType=1">债权项目</a>
                </Menu.Item>
                <Menu.Item>
                    <a rel="noopener noreferrer" href="#/delegateProject/new?assetsType=2">股权项目</a>
                </Menu.Item>
                <Menu.Item>
                    <a rel="noopener noreferrer" href="#/delegateProject/new?assetsType=4">资产转让</a>
                </Menu.Item>
            </Menu>
        );
        const { getFieldDecorator } = this.props.form;
        return (
            <Form layout="inline">
                <FormItem
                    label="关键字"
                >
                    {getFieldDecorator('keyWord')(
                        <Input placeholder="请输入关键字进行查询" />
                    )}
                </FormItem>
                <FormItem
                    label="资金类别"
                >
                    {getFieldDecorator('type', {
                        initialValue: "0"
                    })(
                        <Select style={{width: 80}}>
                            <Option value="0">全部</Option>
                            <Option value="1">债权</Option>
                            <Option value="2">股权</Option>
                            <Option value="4">资产转让</Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem>
                    <Button icon="search" type="primary"  onClick={this.handleSubmit.bind(this,1)} style={{"marginRight": "20px"}}>查询</Button>
                    <Button icon="reload" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button>
                    <Dropdown overlay={menu} placement="bottomCenter">
                        <Button icon="plus">发布新委托</Button>
                    </Dropdown>
                </FormItem>
            </Form>
        )
    }
}

const WrappedSearchForm = Form.create({})(SearchForm);

module.exports = WrappedSearchForm;


